@import "../node_modules/@syncfusion/ej2-base/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-buttons/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-inputs/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-popups/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-react-calendars/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-react-lists/styles/fabric.css";
@import "../node_modules/@syncfusion/ej2-layouts/styles/fabric.css";
@import "../node_modules/@fortawesome/fontawesome-free/css/all.css";

body {
  margin: 0px;
  padding: 0px;
  min-height: 700px;
}

.e-card .e-card-header .e-card-header-image {
  background-image: url("https://ej2.syncfusion.com/react/demos/src/card/images/8.png");
  height: 100px;
  width: 100px;
}

.e-right:before {
  content: "\e913";
}

.e-left:before {
  content: "\e85b";
}

.e-listview .e-list-item {
  background-color: inherit;
  border-bottom: 1px solid #707070;
  border-left: 1px solid #707070;
  border-right: 1px solid #707070;
  border-top: 1px solid#707070;
  margin: 10px;
}

.e-listview .e-list-item.e-hover,
.e-listview .e-list-item.e-hover.e-active.e-checklist {
  border-color: #707070;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  min-height: 700px;
}

.header {
  display: flex;
  flex-direction: row;
  height: 10vh;
  width: 100vw;
  min-height: 70px;
}

.header-text {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3vw;
  font-weight: bold;
  min-height: 70px;
}

.nav {
  height: 10vh;
  width: 10vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vw;
  min-height: 70px;
}

.progress-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 10vh;
  width: 100vw;
  min-height: 100px;
}

.progress-textarea {
  font-size: 2vw;
}

.content-container {
  flex: 1;
  display: flex;
  flex-direction: row;
}

.left-margin,
.right-margin {
  width: 30vw;
}

.content {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.content-textarea {
  font-size: 5vw;
}

.sub-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}

.date-time-pick {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 10px 20px 10px;
}

.time-list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0px 20px 0px;
}
@media (max-width: 500px) {
  .content-textarea {
    font-size: 4vw;
  }

  .progress-textarea {
    font-size: 3vw;
  }

  .header-text {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4vw;
    font-weight: bold;
    min-height: 70px;
  }
}

@media (min-width: 500px) {
  .content-textarea {
    font-size: 2.5vw;
  }
}

@media (max-width: 700px) {
  .left-margin,
  .right-margin {
    width: 10vw;
  }
}

@media (min-width: 700px) {
  .content-textarea {
    font-size: 1.5vw;
  }
}

@media (max-width: 500px) and (max-height: 700px) {
  .left-margin,
  .right-margin {
    width: 10vw;
  }

  .nav {
    height: 10vh;
    width: 20vw;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4vw;
    min-height: 70px;
  }

  .header-text {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5vw;
  }

  .progress-textarea {
    font-size: 4vw;
  }

  .content-textarea {
    font-size: 5vw;
  }
}

.pageSlider-exit > .page {
  z-index: 1;
}

.pageSlider-enter > .page {
  z-index: 10;
}

.pageSlider-enter.left > .page {
  transform: translate3d(100%, 0, 0);
}

.pageSlider-enter.right > .page {
  transform: translate3d(-100%, 0, 0);
}

.pageSlider-exit.left > .page {
  transform: translate3d(-100%, 0, 0);
}

.pageSlider-exit.right > .page {
  transform: translate3d(100%, 0, 0);
}

.pageSlider-enter.pageSlider-enter-active > .page {
  transform: translate3d(0, 0, 0);
  transition: all 800ms ease-in-out;
}

.card-content-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 200px;
  align-items: center;
  justify-content: center;
}

.pay-details,
.select-view-profile {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100px;
  align-items: center;
  justify-content: center;
}

.total,
.plan,
.patient {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  width: calc(100% / 3);
  background-color: #f5f5f5;
  font-weight: 500;
}

.patient {
  font-weight: bolder;
}

.select-dic-filler {
  flex: 1;
}
